import React from "react";
import './Navbar.css';
import { useState, useEffect } from "react";

function Navbar() {

  const [NavbarBackgroundColor, setNavbarBackgroundColor] = useState(false);

  const addBackground = () => {
    if (window.scrollY >= 600) {
      setNavbarBackgroundColor(true)
    }
    else{
      setNavbarBackgroundColor(false)
    }
  }

  window.addEventListener('scroll', addBackground)
  

  const [show, setshow] = useState(true);

  const controlNavbar = () => {
    if (window.scrollY >= 50 && window.scrollY <= 600){
      setshow(false)
    }
    else{
      setshow(true)
    }
  }

  useEffect(() => {
    window.addEventListener('scroll',
    controlNavbar)
    return () => {
      window.removeEventListener('scroll',
      controlNavbar)
    }
  }, [])


  return (
    <nav class={NavbarBackgroundColor ? `nav ${show && 'navbar fixed-top navbar-expand-lg navbar-dark bg-dark p-md-3'}`: `nav ${show && 'navbar fixed-top navbar-expand-lg navbar-dark p-md-4'}`}>
    <div class="container">
      <a class="navbar-brand text-white" href="/"><strong style={{fontSize:"20px"}}>P H L P A T</strong></a>
      {show ? <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
        style={{backgroundColor:"black"}}
      >
        <span class="navbar-toggler-icon"></span>
      </button> : null }

      <div class="collapse navbar-collapse" id="navbarNav">
        <div class="mx-auto"></div>
        <ul class="navbar-nav">
          <div style={{marginTop:"10px"}}></div>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <a className="NavLink" href="#Home">HOME</a>
          </li>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <a className="NavLink" href="#Resume">RESUME</a>
          </li>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <a className="NavLink" href="#Portfolio">PORTFOLIO</a>
          </li>
          <li className="NavItem" style={{marginRight:"20px",paddingLeft:"10px", paddingRight:"10px"}}>
            <a className="NavLink" href="#Contact">CONTACT</a>
          </li>
        </ul>
      </div>
    </div>
  </nav>


  );
}

export default Navbar;